import { v4 as uuidv4 } from "uuid";
import SlsWebLogger from "js-sls-logger-v3";

const opts = {
  host: "us-west-1.log.aliyuncs.com",
  project: "hawkeye-us",
  logstore: "twiclips",
  // logstore: 'pincase-test',
  time: 0.05,
  count: 1,
};

//电脑类型
function detectOS() {
  var sUserAgent = navigator.userAgent;
  var isWin =
    navigator.platform === "Win32" || navigator.platform === "Windows";
  var isMac =
    navigator.platform === "Mac68K" ||
    navigator.platform === "MacPPC" ||
    navigator.platform === "Macintosh" ||
    navigator.platform === "MacIntel";
  if (isMac) return "Mac";
  var isUnix = navigator.platform === "X11" && !isWin && !isMac;
  if (isUnix) return "Unix";
  var isLinux = String(navigator.platform).indexOf("Linux") > -1;
  if (isLinux) return "Linux";
  if (isWin) {
    var isWin2K =
      sUserAgent.indexOf("Windows NT 5.0") > -1 ||
      sUserAgent.indexOf("Windows 2000") > -1;
    if (isWin2K) return "Win2000";
    var isWinXP =
      sUserAgent.indexOf("Windows NT 5.1") > -1 ||
      sUserAgent.indexOf("Windows XP") > -1;
    if (isWinXP) return "WinXP";
    var isWin2003 =
      sUserAgent.indexOf("Windows NT 5.2") > -1 ||
      sUserAgent.indexOf("Windows 2003") > -1;
    if (isWin2003) return "Win2003";
    var isWinVista =
      sUserAgent.indexOf("Windows NT 6.0") > -1 ||
      sUserAgent.indexOf("Windows Vista") > -1;
    if (isWinVista) return "WinVista";
    var isWin7 =
      sUserAgent.indexOf("Windows NT 6.1") > -1 ||
      sUserAgent.indexOf("Windows 7") > -1;
    if (isWin7) return "Win7";
  }
  return "other";
}

//拼接链接
const parseParams = (uri, params) => {
  const paramsArray = [];
  Object.keys(params).forEach(
    (key) => params[key] && paramsArray.push(`${key}=${params[key]}`)
  );
  if (uri.search(/\?/) === -1) {
    uri += `?${paramsArray.join("&")}`;
  } else {
    uri += `&${paramsArray.join("&")}`;
  }
  return uri;
};

//发送埋点
function sendAli(event_category, event_operate, event_tag) {
  chrome.storage.local.get(["ipInfo", "install_time", "uid"], (e) => {
    let sendData = {
      extension_name: "twiclips",
      version: chrome.runtime.getManifest().version,
      user_ip: e.ipInfo?.ip || e.ipInfo?.query || "",
      uuid: e.uid,
      install_time: e.install_time,
      event_time: Math.round(new Date().getTime() / 1000).toString(),
      event_category: event_category || "",
      event_operate: event_operate || "",
      event_tag: event_tag || "",
      browser_type: chrome.runtime.getManifest().homepage_url.includes("chrome")
        ? "chrome"
        : "edge",
      operate_system: detectOS(),
      region: e.ipInfo.country || "",
      language: chrome.i18n.getUILanguage(),
    };
    console.log(sendData);
    const logger = new SlsWebLogger(opts);
    // 发送阿里云日志
    logger.send(sendData);
  });
}

chrome.runtime.onInstalled.addListener(async (detail) => {
  let manifestData = chrome.runtime.getManifest();
  let e = await chrome.storage.local.get([
    "install_time",
    "ipInfo",
    "uid",
    "firstInstall",
  ]);
  let uid = e.uid || uuidv4();
  let install_time =
    e.install_time || Math.round(new Date().getTime() / 1000).toString();
  //初始化基本信息
  await chrome.storage.local.set({
    install_time,
    uid,
  });
  let params = {
    extension: "twitch",
    uuid: uid,
    browser: manifestData.homepage_url.includes("chrome") ? "chrome" : "edge",
    version: manifestData.version,
    install_time,
  };
  // console.log('卸载地址：'+parseParams('http://dev.twiclips.com/twitch-download/question', params));
  chrome.runtime.setUninstallURL(
    parseParams("https://twiclips.com/twitch-download/question", params)
  );

  // 跳转引流页面
  if (!e.firstInstall) {
    // chrome.tabs.create({url:'https://dmsave.top/guideline#download-twitch'})
    chrome.storage.local.set({ firstInstall: true });
  }

  //储存ip地址
  if (!e.ipInfo) {
    // 获取ip信息
    let data = await fetch("https://ipinfo.io/json")
      .then((response) => response.json())
      .catch(() => {
        return "";
      });
    if (data?.status !== 200) {
      data = await fetch("http://ip-api.com/json/")
        .then((response) => response.json())
        .catch(() => {
          return "";
        });
    }
    chrome.storage.local.set({ ipInfo: data });
  }
  if (detail.reason == "install") {
    sendAli("install", "user_install", manifestData.version); //安装统计
  } else if (detail.reason == "update") {
    sendAli("update", "user_update", manifestData.version); //更新统计
  }
});

// import $ from "jquery";
let baseUrl = process.env.VUE_APP_WEB;
////console.log("这是背景页");
var vodDownload = "";

function clipsDownload(url) {
  fetch(baseUrl + "twitch-download/clip?clip_url=" + url)
    .then(function (response) {
      return response.json();
    })
    .then(function (res) {
      // 遍历质量列表，获取最大质量
      let qualityList = Object.keys(res.data.quality);
      let downloadQuality = qualityList[qualityList.length - 1];
      sendMessageToContentScript({
        cmd: "clipsDownloadLink",
        value: res.data.quality[downloadQuality],
      });
    });
}

// eslint-disable-next-line no-unused-vars
browser.runtime.onMessage.addListener((msg, sender, sendResponse) => {
  if (typeof msg === "object" && "action" in msg) {
    switch (msg.action) {
      case "sendAli":
        sendAli(msg.type, msg.control, msg.lable);
        return true;
      case "sendAliDiy":
        sendAliDiy(msg.name, msg.from);
        return true;
      // 保存vod链接
      case "sendVodUrl":
        return (vodDownload = msg.url);
      case "getVodLink":
        sendResponse(vodDownload);
        return (vodDownload = "");
      case "clipsDownload":
        clipsDownload(msg.url);
        // 发送阿里云日志统计
        chrome.storage.local.get(["active_time"], function (res) {
          if (
            res.active_time === "undefined" ||
            new Date(Number(res.active_time) * 1000).toDateString() !==
              new Date().toDateString()
          ) {
            sendAli("active", "user_active", "page");
            chrome.storage.local.set({
              active_time: Math.round(new Date().getTime() / 1000).toString(),
            });
          }
        });
        break;
      case "extensionWarn":
        return fetch("https://twiclips.com/twitch-download/log", {
          method: "post",
          body: JSON.stringify({ request_url: msg.page }),
          headers: {
            "Content-Type": "application/json",
          },
        });
    }
  }
});

function sendAliDiy(name, from) {
  var browserPlat = "";
  if (navigator.userAgent.indexOf("Edg") > -1) {
    browserPlat = "edge";
  } else {
    browserPlat = "chrome";
  }
  var params = {
    APIVersion: "0.6.0",
    website_name: "twiclips-ext",
    event_name: name,
    type: "click",
    from: from,
    event_time: Math.round(new Date().getTime() / 1000).toString(),
    browser_type: browserPlat,
    language: navigator.language || navigator.userLanguage,
  };

  // 将对象形式的params拼接到url上
  var query = Object.keys(params)
    .map(function (key) {
      return encodeURIComponent(key) + "=" + encodeURIComponent(params[key]);
    })
    .join("&");
  var logUrl =
    "https://hawkeye-us.us-west-1.log.aliyuncs.com/logstores/ai-video-website/track";

  var fullUrl = logUrl + "?" + query;

  fetch(fullUrl)
    .then((res) => console.log("res", res))
    .catch((err) => console.log(err));
}

// 给content-script发信息
function sendMessageToContentScript(message, callback) {
  chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
    chrome.tabs.sendMessage(tabs[0].id, message, function (response) {
      if (callback) callback(response);
    });
  });
}

chrome.runtime.onMessageExternal.addListener(function (
  request,
  sender,
  sendResponse
) {
  if (request) {
    if (request.message) {
      if (request.message == "version") {
        const manifest = chrome.runtime.getManifest();
        sendResponse({
          type: "success",
          version: manifest.version,
        });
        return true;
      }
    }
  }
  return true;
});
